import React, {useState} from 'react'
import './Section.css'

function Section({sectionHeight, descFont, setDescFont,backgroundImage, descSize, title, desc, imageWidth, attachment, templateNum, font, backgroundColor,sectionNumber, titleColor, full, bigfont, smallfont, descColor, rate, titleSize, applyButtonUse, applyButtonText, applyButtonColor, targets}) {
    const scrollDown = () => {
        targets.current.scrollIntoView({behavior: 'smooth'})
    };

    switch(parseInt(templateNum)){
        case 1:
            return (
            <div className="make-main-page-section" style={{backgroundImage:`url(${backgroundImage})`, backgroundColor:`${backgroundColor}`, width:'100%', height:`${full ? sectionHeight : sectionHeight*rate }vh`, fontSize:`${full ? bigfont:smallfont}px`
            , marginTop:`${parseInt(sectionNumber) === 1 ? full ? '-11vh' : '-6.5vh' : '0px'}`}}>
                <div className="make-main-section-image">       
                        {attachment && 
                        <div>
                            <img src={attachment} className="make-col-image" style={{width:`${full ? imageWidth : imageWidth*rate }px`}}/>
                        </div>
                        }
                </div>
                <div className="make-main-section-text">
                    <div className="make-middle-section" style={{marginTop:'10%', marginLeft:'5%'}}>
                        <span className="make-one-section-big" style={{fontFamily:`${font}`, color:`${titleColor}`}}>
                            <pre style={{fontFamily:`${font}`, lineHeight:'1.2', fontSize:`${titleSize/50}em`, textAlign:'start'}}>
                                {title}
                            </pre>
                        </span>
                        <span className="make-one-section-small" style={{marginTop:'0%'}}>
                            <pre style={{color:`${descColor}`, fontFamily:`${descFont}`, fontSize:`${(descSize/40)}em`, marginTop:`${full ? 1 : 1*rate}%`, textAlign:'start'}} >
                                {desc}
                            </pre>
                        </span>
                        {applyButtonUse && 
                        <span className="make-apply-button" style={{backgroundColor:`${applyButtonColor}`, width:`${full ? 250 : parseInt(250*rate)}px`,
                        height:`${full ? 40 : 40*rate}px`, fontFamily:`${descFont}`, fontSize:`${full ? 15 : 15*rate}px`}} onClick={e => scrollDown}>
                            {applyButtonText}
                        </span>
                        }
                    </div>  
                </div>
            </div>
            )
        case 2:
            return (
                <div className="make-main-page-section" style={{backgroundImage:`url(${backgroundImage})`, backgroundColor:`${backgroundColor}`, width:'100%', height:`${full ? sectionHeight : sectionHeight*rate }vh`, fontSize:`${full ? bigfont:smallfont}px`, marginTop:`${parseInt(sectionNumber) === 1 ? full ? '-11vh' : '-6.5vh' : '0px'}`}}>
                    <div className="make-main-section-text" style={{textAlign:'center'}}>
                        <div className="make-middle-section" style={{justifyContent:'center',alignItems:'center'}}>
                            <span className="make-one-section-big" style={{fontFamily:`${font}`, textAlign:'center', color:`${titleColor}`}}>
                                <pre style={{fontFamily:`${font}`, lineHeight:'1.2', fontSize:`${titleSize/50}em`, textAlign:'center'}}>
                                    {title}
                                </pre>
                            </span>
                            <span className="make-one-section-small" style={{marginTop:'0%'}}>
                                <pre style={{color:`${descColor}`, fontFamily:`${descFont}`, fontSize:`${(descSize/40)}em`, marginTop:`${full ? 1 : 1*rate}%`, textAlign:'center'}} >
                                    {desc}
                                </pre>
                            </span>
                            {applyButtonUse && 
                            <span className="make-apply-button" style={{backgroundColor:`${applyButtonColor}`, width:`${full ? 250 : parseInt(250*rate)}px`,
                            height:`${full ? 40 : 40*rate}px`, fontFamily:`${descFont}`, fontSize:`${full ? 15 : 15*rate}px`}} onClick={e => scrollDown}>
                                {applyButtonText}
                            </span>
                            }
                        </div>  
                    </div>
                </div>
            )
        case 3:
            return (
                <div className="make-main-page-section" style={{backgroundImage:`url(${backgroundImage})`, backgroundColor:`${backgroundColor}`, width:'100%', height:`${full ? sectionHeight : sectionHeight*rate }vh`, fontSize:`${full ? bigfont:smallfont}px`, marginTop:`${parseInt(sectionNumber) === 1 ? full ? '-11vh' : '-6.5vh' : '0px'}`}}>
                    <div className="make-main-section-text">
                        <div className="make-middle-section" style={{marginTop:'10%', marginLeft:'15%'}}>
                            <span className="make-one-section-big" style={{fontFamily:`${font}`, color:`${titleColor}`}}>
                                <pre style={{fontFamily:`${font}`, lineHeight:'1.2', fontSize:`${titleSize/50}em`, textAlign:'start'}}>
                                    {title}
                                </pre>
                            </span>
                            <span className="make-one-section-small" style={{marginTop:'0%'}}>
                                <pre style={{color:`${descColor}`, fontFamily:`${descFont}`, fontSize:`${(descSize/40)}em`, marginTop:`${full ? 1 : 1*rate}%`, textAlign:'start'}} >
                                    {desc}
                                </pre>
                            </span>
                            {applyButtonUse && 
                            <span className="make-apply-button" style={{backgroundColor:`${applyButtonColor}`, width:`${full ? 250 : parseInt(250*rate)}px`,
                            height:`${full ? 40 : 40*rate}px`, fontFamily:`${descFont}`, fontSize:`${full ? 15 : 15*rate}px`}} onClick={e => scrollDown}>
                                {applyButtonText}
                            </span>
                            }
                        </div>  
                    </div>
                    <div className="make-main-section-image">       
                            {attachment && 
                            <div>
                                <img src={attachment} className="make-col-image" style={{width:`${full ? imageWidth : imageWidth*rate }px`}}/>
                            </div>
                            }
                    </div>
                </div>
            )
        case 4:
            return (
                <div className="make-main-page-section" style={{flexDirection:'column', backgroundImage:`url(${backgroundImage})`, backgroundColor:`${backgroundColor}`, width:'100%', height:`${full ? sectionHeight : sectionHeight*rate }vh`, fontSize:`${full ? bigfont:smallfont}px`, marginTop:`${parseInt(sectionNumber) === 1 ? full ? '-11vh' : '-6.5vh' : '0px'}`}}>
                    <div className="make-main-section-text">
                        <div className="make-middle-section" style={{justifyContent:'center',alignItems:'center'}}>
                            <span className="make-one-section-big" style={{fontFamily:`${font}`, color:`${titleColor}`, textAlign:'center'}}>
                                <pre style={{fontFamily:`${font}`, lineHeight:'1.2', fontSize:`${titleSize/50}em`, textAlign:'center'}}>
                                    {title}
                                </pre>
                            </span>
                            <span className="make-one-section-small" style={{marginTop:'0%'}}>
                                <pre style={{color:`${descColor}`, fontFamily:`${descFont}`, fontSize:`${(descSize/40)}em`, marginTop:`${full ? 1 : 1*rate}%`, textAlign:'center'}} >
                                    {desc}
                                </pre>
                            </span>
                            {applyButtonUse && 
                            <span className="make-apply-button" style={{backgroundColor:`${applyButtonColor}`, width:`${full ? 250 : parseInt(250*rate)}px`,
                            height:`${full ? 40 : 40*rate}px`, fontFamily:`${descFont}`, fontSize:`${full ? 15 : 15*rate}px`}} onClick={e => scrollDown}>
                                {applyButtonText}
                            </span>
                            }
                        </div>  
                    </div>
                    <div className="make-main-section-image" style={{paddingTop:'0px', justifyContent:'start'}}>       
                            {attachment && 
                            <div>
                                <img src={attachment} className="make-col-image" style={{width:`${full ? imageWidth : imageWidth*rate }px`}}/>
                            </div>
                            }
                    </div>
                </div>
            )
    }
}

export default Section
