import React from 'react'

function AskPassword({password, setPassword, getByPassWord}) {
    return (
        <div style={{backgroundColor: 'green', padding:'100px 200px'}}>
            <form onSubmit={() => getByPassWord()}>
                <span>
                확인할 데이터의 비밀번호를 입력하세요.
                </span>
                <span>
                    <input type="password" value={password} onChange={e => setPassword(e.target.value)} />
                </span>
                <button onSubmit={() => getByPassWord()}>입력</button>
            </form>
        </div>
    )
}

export default AskPassword
